:root {
	--primary: #2d8df3;
	--secundary: #63aaf5;
}

.btn {
	padding: 8px 20px;
	border-radius: 4px;
	outline: none;
	border: none;
	font-size: 18px;
	color: white;
	cursor: pointer;
	background-color: var(--primary);
}

.btn:hover {
	padding: 6px 18px;
	transition: all 0.3s ease-out;
	background-color: var(--secundary);
	color: white;
	border-radius: 4px;
	border: 2px solid var(--primary);
}
