.Card {
	border: 3px solid black;
	border-radius: 10px;
	background-color: white;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	flex: 1;
	margin: 10px;
	min-width: 250px;
}

.Image {
	margin: auto 25px;
	background-color: black;
	color: white;
	align-items: center;
	justify-content: space-around;
	display: flex;
	width: 140px;
	height: 140px;
}
.foto {
	height: 145px;
	width: 145px;
	margin-top: 5px;
}

.Descricao {
	margin: 10px;
	color: gray;
	display: flex;
	flex-direction: column;
	flex: 1;
}

.Valor {
	font-weight: 300;
}

.Botao {
	margin: 10px;
}
@media screen and (max-width: 960px) {
	.Card {
		border: 3px solid black;
		border-radius: 10px;
		background-color: white;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		flex: 1;
		margin: 10px;
		min-width: 150px;
	}

	.Image {
		margin: auto 15px;
		background-color: black;
		color: white;
		align-items: center;
		justify-content: space-around;
		display: flex;
		width: 140px;
		height: 140px;
	}
}
