button {
	border: none;
	border-radius: 0.25rem;
	font-size: 0.75rem;
	font-weight: bold;
	padding: 0.75em;
	display: inline-block;
	text-decoration: none;
}
button:hover {
	cursor: pointer;
}
button:focus {
	text-decoration: none;
	outline: none;
}

.comment-box {
	width: 600px;
	max-width: 90%;
	color: rgb(0, 0, 0);
	background-color: #e9ba94;
	border-radius: 0.875rem;
	padding: 0.2rem 1rem 2rem;
	justify-content: start;
	text-align: start;
	font-size: 24px;
}
.comment-form {
	background-color: #fff;
	border-radius: 0.25rem;
	margin-bottom: 1rem;
	padding: 1rem;
	justify-content: start;
	text-align: start;
}
.comment-form-fields {
	margin-bottom: 0.25rem;
	text-align: start;
}
.comment-form-fields textarea {
	border: none;
	border-bottom: 1px solid #404040;
	font-size: 0.85rem;
	padding: 0.25rem 0;
	width: 99%;
	text-align: start;
	margin-top: 0.5rem;
}
.comment-form-fields textarea:focus {
	border-bottom-color: #00bdfc;
	outline: none;
	text-align: start;
}

.comment-count {
	color: #4d6061;
}
#comment-reveal {
	float: right;
	background-color: #00bdfc;
	color: #fff;
}
.comment {
	border-top: 0.125rem solid #404040;
}
.comment-body {
	font-style: italic;
	margin-left: 1rem;
	justify-content: start;
	text-align: start;
	color: rgb(0, 0, 0);
}
.comment-footer {
	margin-bottom: 1rem;
}
.comment-footer-delete {
	padding: 0.2rem 0;
	color: #c90000;
	text-decoration: none;
}
.comment-footer-delete :hover {
	color: #00bdfc;
	font-weight: bold;
}
