.tema {
	position: relative;
	width: 100%;
	background-image: url("../images/fundo-raposa.jpg");
	background-repeat: repeat;
}

.bgtema::before {
	content: "";
	position: absolute;
	top: 0;
	right: 0px;
	bottom: 0px;
	left: 0px;

	background-color: rgba(255, 255, 255, 0.6);
}

.texto h1 {
	position: relative;
	color: rgb(0, 0, 0);
	font-weight: 1600;
	font-size: 2.8rem;
	padding: 3%;
}

.texto h2 {
	position: relative;
	color: rgb(0, 0, 0);
	font-weight: 600;
	font-size: 2rem;
}

.texto h3 {
	position: relative;
	color: rgb(0, 0, 0);
	font-weight: 500;
	font-size: 1.5rem;
	/* padding: 3%; */
}
