* {
	color: rgb(0, 0, 0);
	text-align: center;
	/* font-family: "Courier New", Courier, monospace; */
	font-family: "Great Vibes", cursive;
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.h1 {
	position: relative;
	color: rgb(0, 0, 0);
	font-weight: 1600;
	font-size: 3.5rem;
	padding: 3%;
}

.h2 {
	position: relative;
	color: rgb(0, 0, 0);
	font-weight: 600;
	font-size: 2rem;
}

.h3 {
	position: relative;
	color: rgb(0, 0, 0);
	font-weight: 500;
	font-size: 1.5rem;
}

.pages {
	display: flex;
	height: 90vh;
	align-items: center;
	justify-content: center;
	font-size: 3rem;
}

